body{
    margin: 0;
    padding: 0;
}

.atas{
    text-align: center;
    margin-top: 50px;
    padding: 100px;
}

.bawah{
    text-align: center;
    margin-top: -50px;
}

.containerButton{
    position: relative;
    justify-content: center;
    display: flex;
    flex-direction: row;
}
.but{
    padding: 15px;
    border: none;
    border-radius: 150px;
    position: relative;
    cursor: pointer;
    height: 80px;
    width: 80px;
    margin: 5px;
    box-shadow: 1px 2px 10px gray;
} 

.but:hover{
    cursor: pointer;
    transform: scale(1.040);
    transition: ease-in-out 0.3s;
}
.button{
    display: flex;
    justify-content: center;
    width: 450px;
    height: 160px;
}

.score{
    border: solid;
    display: flex;
    justify-content: center;
    color: white;
}

.player{
    background-color: red;
    width: 150px;
}

.computer{
    background-color: blue;
    width: 150px;
}

.nama{
    font-size: 20px;
}
.angka{
    font: 1em sans-serif;
}

span{
    font-size: 25px;
    color: wheat;
}